

// MAIN BODY

body {
  // max-width: 1000px;
  // width: 90%;
  // margin: auto;

  font-family: Helvetica, Arial, sans-serif;
  font-weight: 300;
  // box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  min-height: 700px;
  padding: 0;
  margin: 0;
}

.container {
  max-width: 800px;
  margin: auto;
  padding: 0px 50px;
}

.body.container {
  padding-bottom: 200px;
}

div.narrow {
  padding: 0px 100px;
}

hr {
  border: none;
  border-top-style: solid;
  border-width: thin;
}





// NAVIGATION

nav {
  background-color: #000;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  z-index: 1;
  // opacity: 0.95;
}

nav div.container {
  max-width: none;
  margin: auto;
}

nav .logotype {
  // position: absolute;
  margin: auto 0;
  padding: 9.5px 0;

  font-size: 20px;
  font-weight: 700;
  color: white;
  letter-spacing: 0.2em;
}

nav div.float-right {
  float: right;
}

nav a {
  display: inline-block;
  position: relative;
  color: #DDD;
  padding: 12px 20px;
  text-decoration: none;
}

// Hovering
nav a:hover {
  color: #FFF;
}

// Current
nav a.current {
  color: #999;
}



nav .dropdown {
  display: inline-block;
  position: relative;
}

nav .dropdown-content {
  display: none;                                      // To Display Dropdowns
  position: absolute;
  padding: 10px 25px;
  min-width: 180px;

  background-color: #FFF;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

nav .dropdown-content a {
  display: block;
  padding: 7px 0;

  color: #555;
  text-decoration: none;
  text-align: left;
}

nav .dropdown-content a * {
  margin: 0;
}


// Hovering
nav .dropdown:hover .dropdown-content {
  display: block;
  left: -5px;
  right: auto;
}

nav .dropdown-content a:hover {
  color: #000;
}

// Current
nav .dropdown .dropdown-content a.current {
  color: #999;
}

// Delimiters
nav .dropdown-content a.delimiter {
  border-top: 2px solid red;
  margin-top: 8px;
  padding-bottom: 2px;
  color: #F00;

  font-size: 0.8em;
  font-weight: normal;
  letter-spacing: 0.1em;
  text-align: center;
}



