// BANNER HEADER

header {
  position: relative;
  background-image: linear-gradient(#000,#444);
  padding: 30px;
}

header.fullscreen {
  position: absolute;
  display: flex;
  padding: 0;
  height: 100vh;
  width: 100vw;
  margin-top: auto;
  margin-bottom: auto;
  align-items: center;
}

header h1, header h2, header h3 {
  color: white;
  text-align: center;
}

header h1 {
  font-size: 4em;
  letter-spacing: -0.03em;
}

header h2 {
  font-size: 2em;
  letter-spacing: -0.01em;
}

header h3 {
  margin-top: 2em;
  margin-bottom: 1em;
  font-size: 1.15em;
  font-weight: 300;
}

header hr {
  border-color: #F00;
  color: #F00;
  width: 20vw;
}



// Headings / Paragraph Styles


h1, h2 {
  color: red;
}

h3, h4, h5, h6 {
  margin-bottom: -7px;
}


h1::before, h2::before, h3::before {
  display: block;
  content: " ";
  margin-top: -50px;
  height: 50px;
  visibility: hidden;
  pointer-events: none;
}

a { color: inherit; }

// p a:visited { color: #999; }

a:hover { color: red; }


.white-on-black {
  background-color: black;
  color: white;
}

h3 .delimiter {
  color: red;
  font-size: 0.8em;
  letter-spacing: 0.1em;
}

h3 a.delimiter {
  border-top:  1px solid red;
  border-top-style: solid;
}


table {
  justify-content: center;
}


p {
  line-height: 130%;
}

ol {
  // margin: 5px 0;
}

li {
  padding: 2px 0;
}

ol ol {
  list-style-type: lower-alpha;
}

.large {
  font-size: large;
}

.x-large {
  font-size: x-large
}

.center {
  text-align: center;
}


// CODE


code {
  font-size: 16px;
}


iframe {
  display: block;
  margin: 0 auto;
}

// EMBEDDED VIDEO

.max-width {
  display: block;
  width: 100%;
  max-width: 500px;
  margin: 20px auto;
  overflow: auto;
}

.aspect-ratio {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  // background: red;
}

.aspect-ratio iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
